body {
  background-color: $whiteColor;
  color: $primaryColor;
  font-family: $primaryfont;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}
img {
  display: block;
}
a {
  text-decoration: none;
}
